import React, { useState } from "react";
import VideoCall from "./VideoCall";

export default function VideoAppointment(props) {
  const { videoCallData } = props;
  const user = JSON.parse(localStorage.getItem("family_doc_app"));
  const [startAppointment, setStartAppointment] = useState(2);
  const [showEndModal, setShowEndModal] = useState(false);

  return (
    <>
      {startAppointment === 2 && (
        <div className="appointment-started">
          <VideoCall
            videoCallData={videoCallData}
            user={user}
            setShowEndModal={setShowEndModal}
          />
        </div>
      )}
    </>
  );
}
